import { useSelector } from "react-redux";
import { selectUserInfo } from "../features/user/userSlice";

export const getName = (users, currentUser) => {
  return users[0].id === currentUser.id ? users[1].name : users[0].name;
};
export const getProfilePic = (users, currentUser) => {
  return users[0].id === currentUser.id
    ? users[1].profilePic
    : users[0].profilePic;
};

export const getFullUser = (users, currentUser) => {
  return users[0].id === currentUser.id ? users[1] : users[0];
};

export const isSameSender = (messages, m, i, userId) => {
  return (
    i < messages.length - 1 &&
    (messages[i + 1].sender.id !== m.sender.id ||
      messages[i + 1].sender.id === undefined) &&
    messages[i].sender.id !== userId
  );
};

export const isLastMessage = (messages, i, userId) => {
  return (
    i === messages.length - 1 &&
    messages[messages.length - 1].sender.id !== userId &&
    messages[messages.length - 1].sender.id
  );
};
