import React from "react";
import Topperbar from "../components/Topperbar";
import Navbar from "../components/Navbar";
import MessageDesktop from "../containers/MessageDesktop";
import Profile from "../components/Profile";

const ProfilePage = () => {
  return (
    <div className="flex bg-[#303841] lg:max-h-screen h-full ">
      <div className="lg:w-1/3 lg:ml-24">
        <Topperbar title="My Profile"></Topperbar>
        <Profile></Profile>
        <Navbar></Navbar>
      </div>
      <div className="lg:w-2/3 lg:visible invisible   lg:relative absolute">
        <MessageDesktop></MessageDesktop>
      </div>
    </div>
  );
};

export default ProfilePage;
