import React, { useState } from "react";
import profile from "../assets/jd-chow-gutlccGLXKI-unsplash.jpg";
import search from "../assets/loupe.png";
import chat from "../assets/conversation.png";
import Search from "../components/Search";
import { useNavigate } from "react-router-dom";
import { selectUserInfo } from "../features/user/userSlice";
import { useSelector } from "react-redux";
const Navbar = () => {
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();
  const user = useSelector(selectUserInfo);
  return (
    <div className="lg:h-screen">
      {" "}
      <div className=" flex flex-around bottom-0 w-full fixed py-7 h-11 bg-[#36404a] space-x-2 lg:fixed lg:top-0 lg:left-0 lg:z-40 lg:w-24 lg:h-screen  lg:transition-transform lg:flex-col lg:justify-center lg:items-center lg:translate-x-0">
        <div
          className="chats  w-1/3 h-full flex  items-center lg:h-1/6"
          onClick={() => navigate("/")}
        >
          <img src={chat} className=" w-5 mx-auto invert" alt="" />
        </div>
        <div
          className="search w-1/3 flex lg:h-1/6  items-center"
          onClick={() => setOpen(!open)}
        >
          <img src={search} className=" w-5 mx-auto invert" alt="" />
        </div>
        <div
          className="profilePic w-1/3 flex  lg:h-1/6 items-center"
          onClick={() => navigate("/profile")}
        >
          <img
            src={user?.profilePic}
            className="w-10 h-10 rounded-full mx-auto lg:h-9 lg:w-9"
            alt=""
          />
        </div>
      </div>
      <Search open={open} setOpen={setOpen}></Search>
    </div>
  );
};

export default Navbar;
