import React, { useState } from "react";
import rightArrow from "../assets/right-chevron.png";
import rightArrowRed from "../assets/right-arrow-red.png";
import downArrow from "../assets/down-arrow.png";
import userLogo from "../assets/user.png";
import logout from "../assets/logout.png";
import { useDispatch, useSelector } from "react-redux";
import {
  selectUserInfo,
  selectUserInfoCheck,
} from "../features/user/userSlice";
import { logoutUserAsync } from "../features/auth/authSlice";
const Profile = () => {
  const [toggleDropDown, setToggleDropDown] = useState(false);
  const user = useSelector(selectUserInfo);
  const userInfoCheck = useSelector(selectUserInfoCheck);
  const dispatch = useDispatch();
  return (
    <>
      {userInfoCheck && (
        <div className="box  lg:max-h-screen h-screen text-white">
          <div className="header  h-1/2 mx-auto w-full flex justify-center items-center ">
            <div className="User  flex flex-col items-center ">
              <img
                src={user.profilePic}
                className="w-32 h-32 rounded-full "
                alt=""
              />
              <h2 className="name font-semibold text-2xl py-2">{user.name}</h2>
              <p className="px-3 text-left mx-3">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempora
                cumque, recusandae officiis quisquam temporibus maiores
                accusantium soluta ducimus culpa excepturi ipsum aperiam magnam
              </p>
            </div>
          </div>
          <div className="about bg-[#36404a] mx-3 rounded-xl cursor-pointer">
            <div
              className="about-box w-full  py-3 px-3 flex justify-between"
              onClick={() => setToggleDropDown(!toggleDropDown)}
            >
              <img src={userLogo} className="w-6 h-6 invert" alt="" />
              <h1>About</h1>
              <img
                className="w-5 invert"
                src={toggleDropDown ? downArrow : rightArrow}
                alt=""
              />
            </div>
            {toggleDropDown && (
              <div className="p-3">
                <div className="name my-2 text-left">
                  <h1>Name</h1>
                  <p>{user.name}</p>
                </div>
                <div className="email my-2 text-left">
                  <h1>Email</h1>
                  <p>{user.email}</p>
                </div>{" "}
                <div className="name my-2 text-left">
                  <h1>Time</h1>
                  <p>{new Date(user.createdAt).toLocaleString()}</p>
                </div>
              </div>
            )}
          </div>
          <div
            className="about bg-[#36404a] mx-3 rounded-xl my-3 cursor-pointer"
            onClick={() => dispatch(logoutUserAsync())}
          >
            <div className="about-box w-full py-3 px-3 flex justify-between">
              <img src={logout} className="w-6 h-6 " alt="" />
              <h1 className="text-red-500">Logout</h1>
              <img className="w-5 h-7 " src={rightArrowRed} alt="" />
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Profile;
