import React, { useState } from "react";

import add from "../assets/plus.png";
import CreateGroupForm from "../containers/CreateGroupForm";
const Topperbar = ({ title }) => {
  const [createGroup, setCreateGroup] = useState(false);
  return (
    <div>
      <div className="chatBar flex justify-between navbar w-full p-3 text-left py-5 bg-[#36404a] top-0   text-white  ">
        <h1 className="text-lg font-mono font-bold">{title}</h1>
        <button
          type="button"
          onClick={() => setCreateGroup(!createGroup)}
          className="focus:outline-none flex items-center justify-center space-x-2 text-white bg-green-700 hover:bg-green-800 focus:ring-4 focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-green-600 dark:hover:bg-green-700 dark:focus:ring-green-800"
        >
          <img src={add} className="invert w-4 h-4 mr-2" alt="" />
          Create new Group
        </button>
      </div>
      <CreateGroupForm
        createGroup={createGroup}
        setCreateGroup={setCreateGroup}
      ></CreateGroupForm>
    </div>
  );
};

export default Topperbar;
