export function fetchCurrentUser() {
  return new Promise(async (resolve) => {
    const response = await fetch("users/fetchCurrentUsers");
    const data = response.json();
    resolve({ data });
  });
}
export function fetchAllUsers(user) {
  return new Promise(async (resolve) => {
    let searchQuery = user;
    const response = await fetch("users/fetchAllUsers?search=" + searchQuery);
    const data = response.json();
    resolve({ data });
  });
}
