import React, { useEffect, useState } from "react";
import { fetchAllUsersAsync, selectUserList } from "../features/user/userSlice";
import { useDispatch, useSelector } from "react-redux";
import cross from "../assets/cross.png";
import {
  addToGroupAsync,
  createGroupAsync,
  removeFromGroupAsync,
  renameGroupAsync,
} from "../features/chat/chatSlice";
import { selectLoggedInUser } from "../features/auth/authSlice";
const CreateGroupForm = ({ updateGroup, setUpdateGroup, currentMessage }) => {
  const [groupName, setGroupName] = useState(
    currentMessage?.chatName ? currentMessage.chatName : ""
  );
  const [search, setSearch] = useState("");
  const userList = useSelector(selectUserList);
  const loggedInUser = useSelector(selectLoggedInUser);
  const dispatch = useDispatch();

  const handleSearch = (e) => {
    let searchQuery = e.target.value;
    searchQuery = searchQuery.charAt(0).toUpperCase() + searchQuery.slice(1);
    setSearch(searchQuery);
  };
  const addUser = (user) => {
    const index = currentMessage.users.findIndex((item) => item.id === user.id);

    if (index < 0) {
      dispatch(addToGroupAsync({ id: currentMessage.id, userId: user.id }));
      return;
    }
    alert("User already exists");
  };

  const cancelHandler = () => {
    setUpdateGroup(!updateGroup);
    setSearch("");
    setGroupName(currentMessage?.chatName);
  };

  const removeHandler = (user) => {
    const index = currentMessage.users.findIndex((item) => item.id === user.id);
    if (index < 0) {
      alert("User don't exists");
      return;
    }
    dispatch(removeFromGroupAsync({ id: currentMessage.id, userId: user.id }));
  };
  const renameHandler = () => {
    if (groupName.length <= 0) {
      alert("Write a group name to update");
      return;
    }
    if (groupName === currentMessage.chatName) {
      alert("Write a modified group name");
      return;
    }
    // console.log({ id: currentMessage.id, chatName: groupName });
    dispatch(renameGroupAsync({ id: currentMessage.id, chatName: groupName }));
  };

  useEffect(() => {
    dispatch(fetchAllUsersAsync(search));
  }, [search]);
  const submitHandler = (e) => {
    e.preventDefault();
  };

  return (
    <div>
      <div>
        <div
          id="authentication-modal"
          className={`fixed top-0 left-0 right-0 z-50 ${
            updateGroup ? null : "hidden"
          } w-full p-4 overflow-x-hidden overflow-y-auto md:inset-0 h-[calc(100%-1rem)] max-h-full`}
        >
          <div className="relative w-full max-w-md max-h-full">
            {/* Modal content */}
            <div className="relative bg-white rounded-lg shadow dark:bg-gray-700">
              <button
                type="button"
                onClick={cancelHandler}
                className="absolute top-3 right-2.5 text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm w-8 h-8 ml-auto inline-flex justify-center items-center dark:hover:bg-gray-600 dark:hover:text-white"
                data-modal-hide="authentication-modal"
              >
                <svg
                  className="w-3 h-3"
                  aria-hidden="true"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 14 14"
                >
                  <path
                    stroke="currentColor"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="m1 1 6 6m0 0 6 6M7 7l6-6M7 7l-6 6"
                  />
                </svg>
                <span className="sr-only">Close modal</span>
              </button>
              <div className="px-6 py-6 lg:px-8">
                <h3 className="mb-4 text-xl font-medium text-gray-900 dark:text-white">
                  Update Group
                </h3>
                <form className="space-y-6" noValidate onSubmit={submitHandler}>
                  <div className="mt-2 flex">
                    <input
                      type="text"
                      required
                      value={groupName}
                      onChange={(e) => setGroupName(e.target.value)}
                      placeholder="Group Name"
                      className="block w-3/4 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                    />
                    <button
                      onClick={renameHandler}
                      className="ml-2  w-1/4 rounded-md block  text-sm   font-medium bg-gray-900 hover:bg-gray-700 text-white"
                    >
                      Update
                    </button>
                  </div>
                  <div>
                    <input
                      type="text"
                      value={search}
                      onChange={(e) => handleSearch(e)}
                      className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-600 dark:border-gray-500 dark:placeholder-gray-400 dark:text-white"
                      placeholder="Search Peoples"
                      required
                    />
                  </div>
                  <div className="addedUsers">
                    {currentMessage.users.map((user) => {
                      return user.id !== loggedInUser.id ? (
                        <span
                          onClick={() => removeHandler(user)}
                          className="inline-flex mx-1 my-1 items-center rounded-md bg-indigo-50 px-2 py-1 text-xs font-medium text-indigo-700 ring-1 ring-inset ring-indigo-700/10"
                        >
                          <h2>{user.name}</h2>
                          <img src={cross} className="h-3 w-3 ml-1" alt="" />
                        </span>
                      ) : null;
                    })}
                  </div>

                  <div className="">
                    {userList.map((user) => (
                      <div
                        className="messageBar  flex p-3  bg-white rounded-lg my-2"
                        onClick={() => addUser(user)}
                      >
                        <div className="profilePic w-1/6 flex ">
                          <img
                            src={user.profilePic}
                            className="w-12 h-12 rounded-full  "
                            alt=""
                          />
                        </div>
                        <div className="name-message w-4/6 text-left flex items-center ">
                          <h2 className="name text-md lg:ml-0 ml-3">
                            {user.name}
                          </h2>
                        </div>
                      </div>
                    ))}
                  </div>

                  <button
                    type="submit"
                    className="w-full text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                  >
                    Leave Group
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateGroupForm;
