import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  accessChat,
  addToGroup,
  createGroup,
  fetchChats,
  fetchCount,
  removeFromGroup,
  renameGroup,
} from "./chatAPI";

const initialState = {
  chats: [],
  status: "idle",
  selectedOrder: null,
};

export const fetchChatsAsync = createAsyncThunk(
  "chat/fetchChats",
  async (amount) => {
    const response = await fetchChats(amount);
    return response.data;
  }
);
export const createGroupAsync = createAsyncThunk(
  "chat/createGroup",
  async (groupInfo) => {
    const response = await createGroup(groupInfo);
    return response.data;
  }
);
export const accessChatAsync = createAsyncThunk(
  "chat/accessChat",
  async (userId) => {
    const response = await accessChat(userId);
    return response.data;
  }
);
export const addToGroupAsync = createAsyncThunk(
  "chat/addToGroup",
  async (addInfo) => {
    const response = await addToGroup(addInfo);
    return response.data;
  }
);
export const removeFromGroupAsync = createAsyncThunk(
  "chat/removeFromGroup",
  async (removeInfo) => {
    const response = await removeFromGroup(removeInfo);
    return response.data;
  }
);
export const renameGroupAsync = createAsyncThunk(
  "chat/renameGroup",
  async (renameInfo) => {
    const response = await renameGroup(renameInfo);
    return response.data;
  }
);

export const chatSlice = createSlice({
  name: "chat",
  initialState,

  reducers: {
    setSelectedOrder: (state, action) => {
      state.selectedOrder = action.payload;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(fetchChatsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchChatsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.chats = action.payload;
      })
      .addCase(accessChatAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(accessChatAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.chats.findIndex(
          (item) => item.id === action.payload.id
        );
        if (index < 0) {
          state.chats.push(action.payload);
        }
        state.selectedOrder = action.payload;
      })
      .addCase(createGroupAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createGroupAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.chats.push(action.payload);
        state.selectedOrder = action.payload;
      })
      .addCase(addToGroupAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addToGroupAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.selectedOrder = action.payload;
      })
      .addCase(removeFromGroupAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(removeFromGroupAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.selectedOrder = action.payload;
      })
      .addCase(renameGroupAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(renameGroupAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.selectedOrder = action.payload;
        const index = state.chats.findIndex(
          (chat) => chat.id === action.payload.id
        );
        state.chats.splice(index, 1, action.payload);
      });
  },
});
export const { setSelectedOrder } = chatSlice.actions;
export const selectChats = (state) => state.chat.chats;
export const selectOrder = (state) => state.chat.selectedOrder;
export default chatSlice.reducer;
