import React, { useEffect } from "react";
import "./App.css";
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom";
import ChatPage from "./pages/ChatPage";
import Signup from "./pages/Signup";
import Login from "./pages/Login";
import ProfilePage from "./pages/ProfilePage";
import Message from "./containers/Message";
import { useDispatch, useSelector } from "react-redux";
import { fetchCurrentUserAsync } from "./features/user/userSlice";
import Protected from "./services/Protected";
import {
  checkAuthAsync,
  selectCheckUser,
  selectLoggedInUser,
} from "./features/auth/authSlice";
import { fetchChatsAsync } from "./features/chat/chatSlice";
import GroupMessage from "./containers/GroupMessage";
const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
        <ChatPage></ChatPage>,
      </Protected>
    ),
  },

  {
    path: "/message",
    element: (
      <Protected>
        <Message></Message>,
      </Protected>
    ),
  },
  {
    path: "/groupmessage",
    element: (
      <Protected>
        <GroupMessage></GroupMessage>,
      </Protected>
    ),
  },
  {
    path: "/profile",
    element: (
      <Protected>
        <ProfilePage></ProfilePage>,
      </Protected>
    ),
  },
  {
    path: "/signup",
    element: <Signup></Signup>,
  },
  {
    path: "/login",
    element: <Login></Login>,
  },
]);
function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const userChecked = useSelector(selectCheckUser);
  useEffect(() => {
    dispatch(checkAuthAsync());
  }, []);
  useEffect(() => {
    if (user) {
      dispatch(fetchCurrentUserAsync());
      dispatch(fetchChatsAsync());
    }
  }, [dispatch, user]);
  return (
    <div className="App">
      {
        <div>
          <RouterProvider router={router} />
        </div>
      }
    </div>
  );
}

export default App;
