import React, { useEffect, useState } from "react";
import profile from "../assets/user.png";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { selectChats, setSelectedOrder } from "../features/chat/chatSlice";
import { selectUserInfo } from "../features/user/userSlice";
import { getName, getProfilePic } from "../services/Common";
import { selectLoggedInUser } from "../features/auth/authSlice";

const Chat = () => {
  const navigate = useNavigate();
  const chats = useSelector(selectChats);
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const [windowDimention, setWindowDimention] = useState({
    winWidth: window.innerWidth,
    winheight: window.innerHeight,
  });
  const [selectedMessage, setSelectedMessage] = useState({});
  const detectSize = () => {
    setWindowDimention({
      winWidth: window.innerWidth,
      winheight: window.innerHeight,
    });
  };
  const handleClick = async (e, chat) => {
    setSelectedMessage(chat);
    if (windowDimention.winWidth < 1024) {
      await dispatch(setSelectedOrder(selectedMessage));
      if (chat.isGroupChat) {
        navigate("/groupmessage");
      } else {
        navigate(`/message`);
      }
    }
  };
  useEffect(() => {
    dispatch(setSelectedOrder(selectedMessage));
  }, [selectedMessage]);

  useEffect(() => {
    window.addEventListener("resize", detectSize);
    return () => {
      window.removeEventListener("resize", detectSize);
    };
  }, [windowDimention]);
  return (
    <div className="chat">
      <div className={`box bg-[#303841] h-full text-white`}>
        {chats.map((chat) => (
          <div
            onClick={(e) => handleClick(e, chat)}
            className={`messageBar ${
              selectedMessage.id === chat.id
                ? "bg-white text-black mx-2 rounded-md"
                : "bg-[#303841]"
            } flex p-3 `}
          >
            <div className="profilePic w-1/6 flex ">
              <img
                src={
                  chat.isGroupChat ? profile : getProfilePic(chat.users, user)
                }
                className="w-12 h-12 rounded-full  "
                alt=""
              />
            </div>
            <div className="name-message w-4/6 text-left ">
              <h2 className="name text-md">
                {chat.isGroupChat ? chat.chatName : getName(chat.users, user)}
              </h2>
              <p className="message text-sm">Hello how are you</p>
            </div>
            <div className="timing">
              <p className="text-sm w-1/6">02:50pm</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Chat;
