import React from "react";
import Topperbar from "../components/Topperbar";
import Navbar from "../components/Navbar";
import MessageDesktop from "../containers/MessageDesktop";
import Chat from "../containers/Chat";

const ChatPage = () => {
  return (
    <div>
      <div className="flex bg-[#303841] lg:h-full h-screen">
        <div className="lg:w-1/3 w-full lg:ml-24">
          <Topperbar title="Chat"></Topperbar>
          <Chat></Chat>
          <Navbar></Navbar>
        </div>
        <div className="lg:w-2/3 lg:visible invisible lg:relative h-full absolute">
          <MessageDesktop></MessageDesktop>
        </div>
      </div>
    </div>
  );
};

export default ChatPage;
