import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchAllUsers, fetchCurrentUser } from "./userAPI";

const initialState = {
  userInfo: null,
  status: "idle",
  userInfoCheck: false,
  userList: [],
};

export const fetchCurrentUserAsync = createAsyncThunk(
  "user/fetchCurrentUser",
  async () => {
    const response = await fetchCurrentUser();
    return response.data;
  }
);
export const fetchAllUsersAsync = createAsyncThunk(
  "user/fetchAllUser",
  async (user) => {
    const response = await fetchAllUsers(user);
    return response.data;
  }
);

export const userSlice = createSlice({
  name: "user",
  initialState,

  reducers: {},

  extraReducers: (builder) => {
    builder
      .addCase(fetchCurrentUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchCurrentUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userInfo = action.payload;
        state.userInfoCheck = true;
      })
      .addCase(fetchAllUsersAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchAllUsersAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.userList = action.payload;
      });
  },
});

export const selectUserInfo = (state) => state.user.userInfo;
export const selectUserInfoCheck = (state) => state.user.userInfoCheck;
export const selectUserList = (state) => state.user.userList;
export default userSlice.reducer;
