import io from "socket.io-client";
export function sendMessage(messageInfo) {
  return new Promise(async (resolve) => {
    const response = await fetch("/messages/sendMessage", {
      method: "POST",
      body: JSON.stringify(messageInfo),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    const socket = io("https://talk-e-tive.onrender.com");
    socket.emit("new message", data);
    resolve({ data });
  });
}
export function fetchMessage(chatId) {
  return new Promise(async (resolve) => {
    const response = await fetch("/messages/fetchMessage/" + chatId);
    const data = await response.json();
    resolve({ data });
  });
}
