import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchCount, fetchMessage, sendMessage } from "./messageAPI";

const initialState = {
  messages: [],
  status: "idle",
  notification: [],
};

export const sendMessageAsync = createAsyncThunk(
  "message/sendMessage",
  async (messageInfo) => {
    const response = await sendMessage(messageInfo);
    return response.data;
  }
);
export const fetchMessageAsync = createAsyncThunk(
  "message/fetchMessage",
  async (chatId) => {
    const response = await fetchMessage(chatId);
    return response.data;
  }
);

export const messageSlice = createSlice({
  name: "message",
  initialState,

  reducers: {
    setSelectedMessage: (state, action) => {
      state.messages.push(action.payload);
    },
    setSelectNotification: (state, action) => {
      state.notification = action.payload;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(sendMessageAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(sendMessageAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.messages.push(action.payload);
      })
      .addCase(fetchMessageAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchMessageAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.messages = action.payload;
      });
  },
});
export const { setSelectedMessage, setSelectNotification } =
  messageSlice.actions;
export const selectMessages = (state) => state.message.messages;
export const selectNotificaiton = (state) => state.message.notification;
export default messageSlice.reducer;
