import React, { useEffect, useState } from "react";
import profile from "../assets/sigmund-jzz_3jWMzHA-unsplash.jpg";
import leftArrow from "../assets/left.png";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { selectOrder, setSelectedOrder } from "../features/chat/chatSlice";
import { selectLoggedInUser } from "../features/auth/authSlice";
import { getFullUser, getName, getProfilePic } from "../services/Common";
import About from "../components/About";
import {
  fetchMessageAsync,
  selectMessages,
  selectNotificaiton,
  sendMessageAsync,
  setSelectNotification,
  setSelectedMessage,
} from "../features/message/messageSlice";
import io from "socket.io-client";
import loadingChat from "../animations/chatLoading.json";
import Lottie from "react-lottie";
// const ENDPOINT = "http://localhost:8080";
// const ENDPOINT = "https://talk-e-tive.vercel.app";
const ENDPOINT = "https://talk-e-tive.onrender.com";
let socket, currentMessageReceived;
const Message = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const currentMessage = useSelector(selectOrder);
  // const notification = useSelector(selectNotificaiton);
  const loggedInUser = useSelector(selectLoggedInUser);
  const fetchedMessages = useSelector(selectMessages);
  const [showAbout, setShowAbout] = useState(false);
  const [message, setMessage] = useState("");
  const [typing, setTyping] = useState(false);
  const [isTyping, setIsTyping] = useState(false);
  const [socketConnected, setSocketConnected] = useState(false);
  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(sendMessageAsync({ content: message, chatId: currentMessage.id }));
    socket.emit("stop typing", currentMessage.id);
    setMessage("");
  };
  const fetchMessages = async () => {
    await dispatch(fetchMessageAsync(currentMessage.id));
    socket.emit("join chat", currentMessage.id);
  };

  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: loadingChat,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  const typingHandler = (e) => {
    setMessage(e.target.value);
    if (!socketConnected) return;
    if (!typing) {
      setTyping(true);
      socket.emit("typing", currentMessage.id);
    }
    let lastTypingTime = new Date().getTime();
    const timerLength = 3000;
    setTimeout(() => {
      let timeNow = new Date().getTime();
      let timeDiff = timeNow - lastTypingTime;
      if (timeDiff >= timerLength && typing) {
        socket.emit("stop typing", currentMessage.id);
        setTyping(false);
      }
    }, timerLength);
  };

  useEffect(() => {
    if (loggedInUser) {
      socket = io(ENDPOINT);
      socket.emit("setup", loggedInUser);
      socket.on("connected", () => setSocketConnected(true));
      socket.on("typing", () => setIsTyping(true));
      socket.on("stop typing", () => setIsTyping(false));
    }
  }, []);

  useEffect(() => {
    if (currentMessage.id) {
      fetchMessages();
    }
  }, [currentMessage]);

  useEffect(() => {
    socket.on("message received", (newMessageReceived) => {
      if (currentMessage.id !== newMessageReceived.chat.id) {
        console.log("This part is working");
        // if (!notification.includes(newMessageReceived)) {
        //   dispatch(
        //     setSelectNotification([newMessageReceived, ...notification])
        //   );
        // }
      } else {
        if (newMessageReceived?.id !== currentMessageReceived) {
          dispatch(setSelectedMessage(newMessageReceived));
          currentMessageReceived = newMessageReceived.id;
        }
      }
    });
  });
  return (
    <div className="w-full overflow: -webkit-scrollbar:none">
      <div
        className="chatBar navbar w-full p-3 text-left py-3 bg-[#36404a] top-0 flex  text-white items-center fixed "
        onClick={() => setShowAbout(!showAbout)}
      >
        <div className="left w-1/6" onClick={() => navigate("/")}>
          <img className="w-7 h-7 invert" src={leftArrow} alt="" />
        </div>
        <div className="middle flex justify-center items-center space-x-4 ">
          <img
            src={getProfilePic(currentMessage.users, loggedInUser)}
            className="w-10 h-10 rounded-full    mx-auto"
            alt=""
          />
          <h1 className="text-lg font-mono font-bold">
            {getName(currentMessage.users, loggedInUser)}
          </h1>
        </div>
      </div>
      <About
        showAbout={showAbout}
        setShowAbout={setShowAbout}
        currentUser={getFullUser(currentMessage.users, loggedInUser)}
      ></About>
      <div className="chatInput w-full bg-[#303841] mt-3">
        <div className="  ">
          <div className="flex-1 mt-14 p:2 sm:p-6 justify-between flex flex-col h-screen ">
            <div
              id="messages"
              className="flex flex-col space-y-4 p-3 overflow-y-auto scrollbar-thumb-blue scrollbar-thumb-rounded scrollbar-track-blue-lighter scrollbar-w-2 scrolling-touch "
            >
              {fetchedMessages &&
                fetchedMessages.map((m, i) => {
                  return m.sender.id === loggedInUser.id ? (
                    <div className="chat-message-sender">
                      <div className="flex items-end justify-end">
                        <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-1 items-end">
                          <div>
                            <span className="px-4 py-2 rounded-lg inline-block rounded-br-none bg-blue-600 text-white ">
                              {m.content}
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="chat-message-receiver">
                      <div className="flex items-end">
                        <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-2 items-start">
                          <div>
                            <span className="px-4 py-2 rounded-lg inline-block rounded-bl-none bg-gray-300 text-gray-600">
                              {m.content}
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
            </div>
            <div className="border-t-2  px-4 pt-4 mb-2 sm:mb-0">
              {isTyping && (
                <div className="chat-message-receiver">
                  <Lottie
                    options={defaultOptions}
                    // height={50}
                    width={40}
                    style={{ marginBottom: 15, marginLeft: 0 }}
                  />
                </div>
              )}
              <form className="relative flex" onSubmit={submitHandler}>
                <input
                  type="text"
                  placeholder="Write your message!"
                  value={message}
                  onChange={typingHandler}
                  className="w-full focus:outline-none focus:placeholder-gray-400 text-gray-600 placeholder-gray-600 pl-3 bg-gray-200 rounded-md py-3"
                />
                <div className="absolute right-0 items-center inset-y-0 hidden sm:flex">
                  <button
                    type="submit"
                    className="inline-flex items-center justify-center rounded-lg px-4 py-3 transition duration-500 ease-in-out text-white bg-blue-500 hover:bg-blue-400 focus:outline-none"
                  >
                    <span className="font-bold">Send</span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 20 20"
                      fill="currentColor"
                      className="h-6 w-6 ml-2 transform rotate-90"
                    >
                      <path d="M10.894 2.553a1 1 0 00-1.788 0l-7 14a1 1 0 001.169 1.409l5-1.429A1 1 0 009 15.571V11a1 1 0 112 0v4.571a1 1 0 00.725.962l5 1.428a1 1 0 001.17-1.408l-7-14z" />
                    </svg>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
        {/* <div className=" flex flex-around bottom-0 w-full fixed py-7 h-11 bg-[#36404a] space-x-2">
          <input type="text" placeholder="message" />
          <button>Helk</button>
        </div> */}
      </div>
    </div>
  );
};

export default Message;
